import { takeLatest, take, race, call, put } from 'redux-saga/effects';
import axios from 'axios';

import vkConnect from '../vkConnect';
import actions from '../actions';

const USER_DENIED_CODE = 4;

function* getPhoneNumber() {
  vkConnect.send('VKWebAppGetPhoneNumber', {});
  const { result, error } = yield race({
    result: take('VKWebAppGetPhoneNumberResult'),
    error: take('VKWebAppGetPhoneNumberFailed')
  });
  if (result) {
    return result.payload.phone_number;
  }
  const { payload: { error_type: errorType, error_data: errorData } } = error;
  if (errorType === 'client_error' && errorData.error_code === USER_DENIED_CODE) {
    return null;
  }
  const errorMessage = errorData.error_reason || errorData.error_msg;
  throw new Error(errorMessage);
}

export default function* placePreOrder({ payload }) {
  try {
    const phone = yield call(getPhoneNumber);
    if (!phone) {
      yield put({ type: actions.PLACE_PRE_ORDER_FAIL, payload: {} });
    }
    const {
      data: {
        Response: { status, message, id: requestId }
      }
    } = yield call(axios, {
      method: 'post',
      url: '/request',
      data: {
        ...payload,
        phone
      }
    });
    if (status !== 'success') {
      throw new Error(message);
    }
    yield put({
      type: actions.PLACE_PRE_ORDER_SUCCESS,
      payload: { requestId, phone }
    });
  } catch (error) {
    yield put({
      type: actions.PLACE_PRE_ORDER_FAIL,
      payload: { error: error.message }
    });
  }
}

export function* watchPlacePreOrder() {
  yield takeLatest(actions.PLACE_PRE_ORDER, placePreOrder);
}
